@charset "UTF-8";
@import url("https://fonts.googleapis.com/css?family=PT+Sans");
html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video { margin: 0; padding: 0; border: 0; vertical-align: baseline; }

html { -ms-text-size-adjust: 100%; -webkit-text-size-adjust: 100%; }

img, fieldset, a img { border: none; }

input[type="text"], input[type="email"], input[type="tel"], textarea { -webkit-appearance: none; }

input[type="submit"], button { cursor: pointer; }

input[type="submit"]::-moz-focus-inner, button::-moz-focus-inner { padding: 0; border: 0; }

textarea { overflow: auto; }

input, button { margin: 0; padding: 0; border: 0; }

div, input, textarea, select, button, h1, h2, h3, h4, h5, h6, a, span, a:focus { outline: none; }

ul, ol { list-style-type: none; }

table { border-spacing: 0; border-collapse: collapse; width: 100%; }

html { box-sizing: border-box; }

*, *:before, *:after { box-sizing: inherit; }

@media (prefers-reduced-motion: reduce) { * { transition: none !important; } }

.icon-facebook { width: 0.53em; height: 1em; fill: #000; }

body { background: #FFF; font-size: 50px; font-size: calc(16px + (50 - 16) * ((100vw - 600px) / (1000 - 600))); line-height: 1.4; }

@media (max-width: 600px) { body { font-size: 16px; } }

@media (min-width: 1000px) { body { font-size: 50px; } }

@font-face { font-family: Fregat; src: url("../fonts/Fregat.ttf"); }

@font-face { font-family: Fregat-bold; src: url("../fonts/Fregat_bold.ttf"); }

@font-face { font-family: DevanagariMT; src: url("../fonts/DevanagariMT.ttf"); }

h2 { font-family: Fregat-bold; font-size: 24px; color: #4cb027; line-height: 1.2; text-transform: uppercase; margin-bottom: 29px; }

h3 { font-family: Fregat; font-size: 18px; color: white; text-transform: uppercase; line-height: 1.5; text-align: left; }

p { font-size: 17px; font-family: 'PT Sans', sans-serif; color: #4e4e4e; line-height: 1.395; text-align: left; margin-bottom: 25px; }

.btn { font-size: 13px; font-family: 'PT Sans', sans-serif; color: #4cb027; font-weight: bold; text-transform: uppercase; line-height: 1.606; text-align: center; border: 2px solid; padding: 11px 17px; border-radius: 4px; }

.buttons a { text-decoration: none; }

.btn:hover { background-color: #4cb027; color: white; border: 2px solid #4cb027; }

a { color: #000; }

.header { background-color: #fff; max-width: 1175px; }

.header ul { margin: 0; padding: 0; list-style: none; overflow: hidden; background-color: #fff; }

.header li a { display: block; text-decoration: none; font-family: Fregat; font-size: 13px; color: #363636; line-height: 1.2; text-transform: uppercase; margin-top: 16px; }

.header li a:hover { color: #4cb027; }

.menu-btn:hover { background-color: #f4f4f4; }

.header__logo { display: block; float: left; text-decoration: none; font-size: 40px; margin-left: 14px; }

.menu { clear: both; max-height: 0; transition: max-height .2s ease-out; text-align: center; }

.menu-icon { cursor: pointer; display: inline-block; float: right; padding: 28px 20px; position: relative; user-select: none; }

.navicon { background: #333; display: block; height: 2px; position: relative; transition: background .2s ease-out; width: 18px; }

.navicon:before, .navicon:after { background: #333; content: ''; display: block; height: 100%; position: absolute; transition: all .2s ease-out; width: 100%; }

.navicon:before { top: 5px; }

.navicon:after { top: -5px; }

.menu-btn { display: none; }

.menu-btn:checked ~ .menu { max-height: 312px; }

.menu-btn:checked ~ .menu-icon .navicon { background: transparent; }

.menu-btn:checked ~ .menu-icon .navicon:before { transform: rotate(-45deg); }

.menu-btn:checked ~ .menu-icon .navicon:after { transform: rotate(45deg); }

.menu-btn:checked ~ .menu-icon:not(.steps) .navicon:before, .menu-btn:checked ~ .menu-icon:not(.steps) .navicon:after { top: 0; }

.menu__lastitem { margin-bottom: 32px; }

.header:after { content: ""; display: table; clear: both; }

@media (min-width: 1084px) { .header { margin: 0 auto; }
  .header li { float: left; }
  .menu { clear: none; float: right; max-height: none; }
  .menu-icon { display: none; }
  .menu__item:after { content: "•"; margin-right: 7px; margin-left: 7px; }
  .menu__lastitem { margin-right: 4px; margin-bottom: 0; } }

.about img { width: 100%; }

.about p { padding: 10px; }

.about__buttons { text-align: center; display: flex; flex-direction: column; }

.about__buttons a { margin-bottom: 32px; }

.forwhom { background-color: #4cb027; display: flex; flex-direction: column; justify-content: space-between; font-family: 'PT Sans', sans-serif; font-size: 13px; color: white; line-height: 1.5; text-align: left; }

.forwhom__headline { padding: 83px 0 40px 0; }

.forwhom__headline h3 { text-align: center; }

.forwhom__list { padding: 35px; text-align: center; }

.forwhom__listul { list-style-image: url("../img/ul.png"); }

.what { padding-top: 55px; }

.what h2 { text-align: center; font-family: Fregat; font-size: 27px; margin-bottom: 19px; }

.what p { font-family: 'PT Sans', sans-serif; font-size: 17px; color: #313131; line-height: 1.474; text-align: center; width: 64%; margin: 0 auto; }

.slider img { width: 100%; }

.slider { margin-top: 28px; }

.what__list { padding: 50px 8px 50px 50px; }

.what__listul { list-style-image: url("../img/ul2.png"); font-size: 14px; color: #4e4e4e; font-family: 'PT Sans', sans-serif; }

.what__listul li { maargin-bottom: 9px; }

@media (min-width: 960px) { .about__img { display: none; }
  .about { background-image: url("../img/workout.png"); background-size: cover; min-height: 268px; padding: 97px 50% 22px 33px; }
  .about__headline h2 { color: #4cb027; }
  .about p { width: 81%; padding: 0; }
  .about__buttons { display: block; text-align: left; }
  .forwhom { flex-direction: row; }
  .forwhom__list { text-align: left; width: 54%; }
  .forwhom__headline { padding-left: 87px; padding-bottom: 87px; }
  .forwhom__headline h3 { text-align: left; }
  .what__list { display: flex; }
  .ul-col2 { margin-left: 32px; } }
